.projects {
	width: 100%;
	padding-top: 2em;
	display: flex;
	flex-direction: column;

	justify-content: space-evenly;
	align-items: center;
	.project-card {
		margin-top: 2em;
		margin-bottom: 5em;
		position: relative;
		display: grid;
		color: white;
		padding: 1em;
		grid-template-columns: 1fr 400px;
		grid-template-rows: 1fr 10px;
		background-color: #364f6b;
		max-width: 900px;
		min-height: 270px;
		img {
			max-width: 100%;
			max-height: 230px;
		}
		.img-container {
			grid-column: 2/3;
			grid-row: 1/2;
			display: flex;
			justify-content: center;
			max-width: 100%;
		}
		.tech-names {
			color: rgb(0, 255, 242);
			font-weight: 500;
			padding-top: 10px;

			font-family: Roboto mono;
		}
		.links-container {
			position: relative;
			height: 70px;
			display: flex;
			align-items: center;
			justify-content: space-evenly;
			max-width: 40%;
			left: 2em;
			top: -15px;
			grid-column: 1/3;
			grid-row: 2/3;
			background-color: #fc5185;
			a {
				background-color: black;
				padding: 0.6em 1em;
				color: white;
				font-size: 1.2em;
				font-weight: 800;
				text-decoration: none;
				transition: 200ms ease-in-out 0ms;
				&:hover {
					background-color: #43dde6;
					color: black;
				}
				&:focus {
					outline: none;
				}
			}
		}
		.text-container {
			grid-column: 1/ 2;
			grid-row: 1/2;
			font-size: 1.3em;
			padding-right: 20px;
		}
	}
}

@media (max-width: 1020px) {
	.projects {
		.project-card {
			grid-template-columns: 1fr;
			grid-template-rows: 1fr 200px 10px;
			max-width: 100vw;
			.img-container {
				grid-row: 1/2;
				grid-column: 1/2;
			}
			.text-container {
				grid-column: 1/ 2;
				justify-self: center;
				grid-row: 2/3;
				font-size: 1.3em;
				max-width: 400px;

				text-align: center;
				padding-right: 20px;
				padding-top: 15px;
				font-size: 1em;
				line-height: 25px;
				.tech-names {
					padding-top: 0;
				}
			}
			.links-container {
				grid-column: 1/2;
				grid-row: 3/4;
				align-self: center;
				max-width: 50em;
				top: 20px;
				left: 0;
				position: relative;
			}
		}
	}
}
@media (max-width: 500px) {
	.projects {
		.project-card {
			grid-template-columns: 1fr;
			grid-template-rows: 1fr 200px 10px;
			.img-container {
				img {
					max-height: 257px;
				}
			}
			.text-container {
				max-width: none;
			}
		}
	}
}
